
html,body,div,span,applet,main,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%
}
article,aside,details,main,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block
}
blockquote,q{quotes:none
}
blockquote:before,blockquote:after{content:'';content:none
}
q:before,q:after{content:'';content:none
}
table{border-collapse:collapse;border-width:0;padding:0;margin:0
}
html{height:100%;-webkit-text-size-adjust:none;-ms-text-size-adjust:none
}
input,textarea{color:#333;outline:none;border-radius:0;-moz-border-radius:0;-webkit-border-radius:0;-webkit-appearance:none
}
input[type=button],input[type=submit]{cursor:pointer;-webkit-appearance:none;outline:none
}
input[type=search]{-webkit-appearance:none
}
button{cursor:pointer;-webkit-appearance:none;outline:none
}
td,form{margin:0;padding:0
}
a,i{color:#000100;-webkit-transition:all .3s linear;transition:all .3s linear;text-decoration:none;outline:none;-webkit-tap-highlight-color:rgba(0,0,0,0);display:inline-block
}
*{-webkit-box-sizing:border-box;box-sizing:border-box
}
*:before,*:after{-webkit-box-sizing:border-box;box-sizing:border-box
}
ul{list-style:none
}
span{display:inline-block
}
img{max-width:100%
}
body{height:1000px;font-size:16px;font-family:'Helvetica', 'Arial', sans-serif
}
.flex-container{display:-webkit-box;display:-ms-flexbox;display:flex;min-height:100vh
}

.nav-row{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-box-align:center;-ms-flex-align:center;align-items:center;background:#c4c4c4;text-transform:uppercase
}
.menu-list{display:-webkit-box;display:-ms-flexbox;display:flex
}
.menu-list__link{position:relative;display:block;padding:15px;color:#333;white-space:nowrap
}
.menu-list__link:after{content:'';position:absolute;left:0;bottom:0;display:block;height:3px;background:#000;width:0;-webkit-transition:.3s ease-in-out;transition:.3s ease-in-out
}
.menu-list__link:hover:after{width:100%
}
.user-profile{padding:15px
}

.side-menu{padding:30px 0;background:#f3f3f3;width:250px;height:100%
}
.side-menu__item{margin-bottom:20px
}
.side-menu__item:last-child{margin-bottom:0
}
.side-menu__link{display:block;padding:10px 20px;-webkit-transition:background-color .3s ease;transition:background-color .3s ease
}
.side-menu__link:hover{background:#c4c4c4
}
.router-link-exact-active{background:#c4c4c4;font-weight:bold
}

.viewport{max-width:1140px;width:100%;height:100%
}
.viewport-body h3{text-align:center;font-size:20px;font-weight:400;margin-bottom:50px
}
.side-menu__content{height:100%;padding:40px
}

.instruments-container{position:relative;padding:0 60px
}
.btn{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;width:250px;padding:10px;border:1px solid #000;border-radius:8px;background:#e5e5e5;height:120px
}
.btn img{width:40px;margin-bottom:15px
}
.instruments-row{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;margin-bottom:20px
}
.instruments-row:last-child{margin-bottom:0
}

.card{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;width:250px;height:120px;padding:10px 15px;background:#fff;border:1px solid #000;border-radius:8px;margin:0 15px 15px 0;cursor:default
}
.card-row{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between
}
.card-instruments span{position:relative;padding:5px;cursor:pointer
}
.card-instruments span:before{content:'';position:absolute;z-index:10;width:100%;height:100%;top:0;left:0
}
.card-instruments img{width:15px
}
.card-instruments svg{width:15px;height:15px
}
.card-status{font-size:12px;margin-left:auto
}
.card-status--confirmed{color:#79D98A
}
.card-status--unconfirmed{color:red
}
.card-num{font-size:22px;text-align:center
}
.card-name{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center
}
.card-logo{width:20px;height:20px;margin-right:5px
}
.card-brand{width:30px
}
.card-brand img{display:block
}

.popup{position:absolute;top:50%;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);width:400px;z-index:100;padding:40px 40px 30px;background:#fff;-webkit-box-shadow:0 0 8px rgba(0,0,0,0.3);box-shadow:0 0 8px rgba(0,0,0,0.3)
}
.popup-close{position:absolute;top:5px;right:5px;z-index:10;padding:15px;cursor:pointer
}
.popup-close:before,.popup-close:after{position:absolute;content:'';display:block;width:20px;height:1px;background:#000
}
.popup-close:before{-webkit-transform:translate(-50%, -50%) rotate(-45deg);transform:translate(-50%, -50%) rotate(-45deg)
}
.popup-close:after{-webkit-transform:translate(-50%, -50%) rotate(45deg);transform:translate(-50%, -50%) rotate(45deg)
}
.popup h4{text-align:center;margin-bottom:30px
}
.popup-row{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;width:100%;margin-bottom:20px
}
.popup__label{margin-bottom:10px
}
.popup__input{width:100%;padding:10px;border:1px solid rgba(0,0,0,0.2);border-radius:4px
}
.popup__input:focus{-webkit-box-shadow:0 0 6px deepskyblue;box-shadow:0 0 6px deepskyblue
}
.popup__input-wrapper{width:100%;border-radius:4px;border:1px solid rgba(0,0,0,0.2);overflow:hidden
}
.popup__input-wrapper.not-valid{border-color:red;-webkit-text-decoration:underline dotted red;text-decoration:underline dotted red;-webkit-box-shadow:0 0 6px red;box-shadow:0 0 6px red
}
.popup__input-wrapper.not-valid-title{background:red;color:#fff
}
.popup__input-wrapper.valid{-webkit-box-shadow:0 0 6px green;box-shadow:0 0 6px green;border-color:green
}
.popup__input-wrapper.valid-title{background:green;color:#fff
}
.popup__input-wrapper.focused{-webkit-box-shadow:inset 0 0 6px deepskyblue;box-shadow:inset 0 0 6px deepskyblue
}
.popup__input-wrapper .popup__input{border:none;width:calc(100% + 40px)
}
.popup__btns-group{display:-webkit-box;display:-ms-flexbox;display:flex
}
.popup .popup__btn{display:-webkit-box;display:-ms-flexbox;display:flex;margin:0 auto;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;padding:10px;width:100px;border:none;-webkit-transition:.3s;transition:.3s
}
.popup .popup__btn-add{-webkit-box-shadow:0 0 2px;box-shadow:0 0 2px
}
.popup .popup__btn-add.active{color:#fff;background:#28a745;-webkit-box-shadow:0 0 4px 1px rgba(40,167,69,0.8);box-shadow:0 0 4px 1px rgba(40,167,69,0.8)
}
.popup .popup__btn-add.active:hover,.popup .popup__btn-add.active:focus{background:#218838
}
.popup .popup__btn-remove{background:#d9534f;color:#fff;margin-right:15px
}
.popup .popup__btn-remove:hover,.popup .popup__btn-remove:focus{background:#c9302c
}
.popup .popup__btn-cancel{color:#fff;background:#0275d8
}
.popup .popup__btn-cancel:hover,.popup .popup__btn-cancel:focus{background:#025aa5
}
.popup .popup__btn-save{display:-webkit-box;display:-ms-flexbox;display:flex;background:#28a745;color:#fff
}
.popup .popup__btn-save:hover,.popup .popup__btn-save:focus{background:#218838
}

.validate-errors{position:absolute;z-index:100;right:30px;top:54%;-webkit-transform:translate(100%);transform:translate(100%);width:180px;background:rgba(217,83,79,0.9);color:#fff;border-radius:4px;-webkit-box-shadow:1px 2px 6px rgba(0,0,0,0.3);box-shadow:1px 2px 6px rgba(0,0,0,0.3);font-size:12px
}
.validate-errors__list{padding:20px 10px
}
.validate-errors__item{margin-bottom:20px
}
.validate-errors__item:last-child{margin-bottom:0
}

.popup-remove{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-align:center;-ms-flex-align:center;align-items:center
}

.address-container{padding:0 60px
}
.address-form{padding:15px
}
.address-form__row{position:relative;padding:35px 20px 15px;margin-bottom:20px
}
.address-form__label{position:absolute;top:0;left:0;display:block;width:100%;height:100%;border:1px solid rgba(196,196,196,0.7);border-radius:6px
}
.address-form__label:before{content:attr(data-require);position:absolute;top:50%;left:20px;-webkit-transform:translateY(-50%);transform:translateY(-50%);font-size:18px;-webkit-transition:.2s;transition:.2s;color:#8FA0B7;letter-spacing:.3px
}
.address-form__multiply{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between
}
.address-form__multiply .address-form__row{width:calc(100% / 3 - 15px)
}
.address-form__input{width:100%;border:none;font-size:18px
}
.address-form__input:focus ~ .address-form__label{border-color:#00bfff;-webkit-box-shadow:0 0 6px #00bfff;box-shadow:0 0 6px #00bfff
}
.address-form__input:focus ~ .address-form__label:before,.address-form__input:valid ~ .address-form__label:before{font-size:14px;top:20px
}
.open{position:absolute;cursor:pointer;z-index:10;top:calc(50% - 15px);right:20px;padding:15px;border-radius:50%;-webkit-transition:.2s ease-in-out;transition:.2s ease-in-out
}
.open:before,.open:after{content:'';position:absolute;top:50%;left:calc(50% - 6px);display:block;width:8px;height:2px;background:#828282
}
.open:before{-webkit-transform-origin:3px 0;transform-origin:3px 0;-webkit-transform:rotate(45deg);transform:rotate(45deg)
}
.open:after{-webkit-transform-origin:7px -4px;transform-origin:7px -4px;-webkit-transform:rotate(-45deg);transform:rotate(-45deg)
}
.open.active{-webkit-transform:rotate(-180deg);transform:rotate(-180deg)
}
.result-list{display:-webkit-box;display:-ms-flexbox;display:flex
}
.result-list__item{margin-right:5px;font-weight:bold
}
